import React, { useState, useRef, useEffect, useContext } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { Form, Row, Col, Button } from 'react-bootstrap';
import { useSelector, useDispatch } from 'react-redux'
import { Checkbox } from 'primereact/checkbox';
import * as FontAwesome from 'react-icons/fa'
import allActions from '../../actions'

import apphelper from '../../apphelper/apphelper'
import HeaderPage from '../headerpage'
import gbstyle from '../../assets/gbstyle';
import ApiContext from '../../apphelper/apihelper';
import { BankDTO } from '../../dto/master/bankdto';

export default function FrmBankManage() {
    const apicontext = useContext(ApiContext);
    const systemenv = useSelector(state => state.systemenv)
    const userstate = useSelector(state => state.user)
    const [formvar, setformvar] = useState(apphelper.InitFormVar());
    const [dto, setdto] = useState(new BankDTO());
    let navigate = useNavigate();
    let { param } = useParams();

    useEffect(() => {
        InitForm();
    }, []);

    const InitForm = () => {
        //console.log(dto)
        let obj = apicontext.CheckParams(param, 'bank');
        if (apphelper.IsNull(obj)) {
            navigate(apphelper.NavigatPath.frmerror)
            return;
        }

        setformvar({
            ...formvar,
            formname: 'ข้อมูลธนาคาร',
            formstate: obj.formstate
        })

        switch (obj.formstate) {
            case apphelper.FormState.ADD:
                break;
            case apphelper.FormState.EDIT:
                setdto(obj.data);
                break;
        }
        // setdto(obj)

        apicontext.BindNextTab()
    }

    const doSave = async () => {
        let result = {}
        let _dto = {...dto}
        _dto.CreateBy = userstate.staffid        

        switch (formvar.formstate) {
            case apphelper.FormState.ADD:
                result = await apicontext.ApiPost(systemenv.apiurl + "bank/add", _dto)
                break;
            case apphelper.FormState.EDIT:
                result = await apicontext.ApiPost(systemenv.apiurl + "bank/edit", _dto)
                break;
        }

        if (result.success)
            navigate(apphelper.NavigatPath.bank)

    }


    // const gridHeaderRender = () => {
    //     return (
    //         <GridHeaderButton addshow={true} addclick={doAdd} editshow={true} delshow={true} />
    //     )
    // }

    return (
        <div>
            <HeaderPage frmname={formvar.formname} formstate={formvar.formstate} compname='Comp Test' branchname='Branch Test' />

            <Form.Group as={Row} className="form-row">
                <Col md={1}>
                    <Form.Label className="lb lb-validate">รหัสธนาคาร</Form.Label>
                </Col>

                <Col md={2}>
                    <Form.Control className="nexttab" type="text" readOnly={!formvar.IsAddState()} onKeyDown={apicontext.NextTab} value={dto.BankID} onChange={(e) => setdto({ ...dto, BankID: e.target.value })} />
                </Col>
            </Form.Group>

            <Form.Group as={Row} className="form-row">
                <Col md={1}>
                    <Form.Label className="lb lb-validate">ชื่อธนาคาร</Form.Label>
                </Col>

                <Col md={8}>
                    <Form.Control className="nexttab" type="text" onKeyDown={apicontext.NextTab} value={dto.BankName} onChange={(e) => setdto({ ...dto, BankName: e.target.value })} />
                </Col>
            </Form.Group>

            <Form.Group as={Row} className="form-row">
                <Col md={1}>
                    <Form.Label className="lb">UseStatus</Form.Label>
                </Col>

                <Col md={3}>
                    <Checkbox checked={apphelper.StringToBool(dto.UseStatus)} onChange={e => setdto({ ...dto, UseStatus: apphelper.BoolToString(e.checked) })}></Checkbox>
                </Col>
            </Form.Group>

            <Form.Group as={Row}>
                <Col style={{ ...gbstyle.aligncenter, ...gbstyle.alignitemcenter }}>
                    <Button variant="primary" size="sm" onClick={() => doSave()}><FontAwesome.FaSave className="btnicon" /> Save</Button>

                    <Button variant="info" size="sm" className="nextitem" onClick={() => navigate(apphelper.NavigatPath.bank)}><FontAwesome.FaChevronCircleLeft className="btnicon" /> Cancel</Button>
                </Col>
            </Form.Group>


            {/* <SearchBox condlist={condlist} searchevent={doSearch} /> */}

            {/* <DataTable value={datalist} header={gridHeaderRender()} paginator={true} rows={apphelper.ConfigVar.GridRow} paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink" scrollable={true}>
                <Column field="BankID" style={{ width: 300 }} header="รหัส" />
                <Column field="BankName" header="ชื่อ" />
                <Column field="UseStatus" header="UseStatus" style={ gbstyle.usestatuscal } />
            </DataTable> */}

            <br />

        </div>
    )
}
