import React, { useState, useContext, useEffect, useCallback, useMemo } from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import { createBrowserHistory } from "history";
import { useSelector } from 'react-redux'

import allActions from './actions'

import FrmLogIn from './component/login/frmlogin'
import Home from './component/home'
import FrmError from './component/frmerror'
import FrmExpense from './component/master/frmexpense';
import FrmCheque from './component/master/frmcheque';
import FrmExpenseManage from './component/master/frmexpensemanage';
import FrmBank from './component/master/frmbank';
import FrmBankManage from './component/master/frmbankmanage';
import Frm1001 from './component/cheque/frm1001'
import { QueryParamProvider } from 'use-query-params';

const GuardedRoute = (props) => (
    // <Route path={props.path} element={props.auth === true ? props.element : document.location.href = document.location.protocol + '//' + document.location.host + '/login'} />
    
    <Route path={props.path} element={props.auth === true ? props.element : props.element} />
)

const hist = createBrowserHistory();

export default function RootRoute() {
    const [islogin, setislogin] = useState(true);

    useEffect(() => {
        //console.log('use effect rootroute')     
        if (localStorage.getItem('isLoggedIn_icheque') === null || localStorage.getItem('isLoggedIn_icheque') === undefined) {
            setislogin(false)
        } 
    }, []);    

    return (
        <BrowserRouter>
            <Routes>
                <Route path={process.env.PUBLIC_URL} element={islogin ? <Home /> : <FrmLogIn />} />
                <Route path={process.env.PUBLIC_URL + "/login"} element={<FrmLogIn />} />
                <Route path={process.env.PUBLIC_URL + "/home"} element={<Home />} >
                    <Route path={"/expense"} element={<FrmExpense />} />
                    <Route path={"/expensemanage/:param"} element={<FrmExpenseManage />} />
                    <Route path={"/bank"} element={<FrmBank />} />
                    <Route path={"/bankmanage/:param"} element={<FrmBankManage />}/>
                    <Route path={"/cheque"} element={<FrmCheque />} />

                    <Route path={"/frm1001"} element={<Frm1001 />} />

                    <Route path={"/frmerror"} element={<FrmError />} />
                </Route>

                {/* <GuardedRoute path="/home" element={<Home />} auth={user.islogin}>
                    <GuardedRoute path="/expense" element={<FrmExpense />} auth={user.islogin} />
                    <GuardedRoute path="/expensemanage/:param" element={<FrmExpenseManage />} auth={user.islogin} />
                    <GuardedRoute path="/bank" element={<FrmBank />} auth={user.islogin} />
                    <GuardedRoute path="/bankmanage/:param" element={<FrmBankManage />} auth={user.islogin} />
                    <GuardedRoute path="/cheque" element={<FrmCheque />} auth={user.islogin} />
                    <Route path="/frmerror" element={<FrmError />} />
                </GuardedRoute> */}
            </Routes>
        </BrowserRouter>
    )
}



