import React, { useState, useRef, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Form, Row, Col, Button } from 'react-bootstrap';
import { useSelector, useDispatch } from 'react-redux'
import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';
import allActions from '../../actions'

import apphelper from '../../apphelper/apphelper'
import HeaderPage from '../headerpage'
import SearchBox from '../searchbox'
import GridHeaderButton from '../gridheaderbutton'

export default function FrmExpense() {
    const dispatch = useDispatch()
    const [formvar, setformvar] = useState(apphelper.InitFormVar());
    const [datalist, setdatalist] = useState([]);
    let navigate = useNavigate();
    let param = { id: 'idtest', name: 'nametest' }

    let condlist = [
        { value: 'ID', label: 'รหัส' },
        { value: 'Name', label: 'ชื่อ' }
    ]

    const [datadummy, setdatadummy] = useState([
        { ID: 'id1', BankName: 'name 1' },
        { ID: 'id2', BankName: 'name 1' },
        { ID: 'id3', BankName: 'name 1' },
        { ID: 'id4', BankName: 'name 1' },
        { ID: 'id5', BankName: 'name 1' },
        { ID: 'id6', BankName: 'name 1' },
        { ID: 'id7', Name: 'name 1' },
        { ID: 'id8', Name: 'name 1' },
        { ID: 'id9', Name: 'name 1' },
        { ID: 'id10', Name: 'name 1' },
        { ID: 'id11', Name: 'name 1' },
        { ID: 'id12', Name: 'name 1' },
        { ID: 'id13', Name: 'name 1' },
        { ID: 'id14', Name: 'name 1' },
        { ID: 'id15', Name: 'name 1' },
        { ID: 'id16', Name: 'name 1' },
        { ID: 'id17', Name: 'name 1' },
        { ID: 'id18', Name: 'name 1' },
        { ID: 'id19', Name: 'name 1' },
        { ID: 'id20', Name: 'name 1' },
        { ID: 'id21', Name: 'name 1' },
        { ID: 'id22', Name: 'name 1' },
    ]);

    useEffect(() => {
        InitForm();
    }, []);

    const InitForm = () => {
        setformvar({
            ...formvar,
            formname: 'ข้อมูลค่าใช้จ่าย',
            formstate: apphelper.FormState.ADD
        })

        doSearch(undefined)
    }

    const doSearch = (params) => {
        if (!apphelper.IsNull(params))
            setdatalist(datadummy.filter(data => data.ID.includes(params.searchtext)))
        else
            setdatalist(datadummy);
    }

    const doAdd = () => {
        navigate(apphelper.NavigatPath.expensemanage + apphelper.Encode(JSON.stringify(param)))
    }


    const showLoading = () => {
        dispatch(allActions.loadingAction.showloading(apphelper.LoadingText.Save))

        setTimeout(() => {
            dispatch(allActions.loadingAction.hideloading())
        }, 3000);
    }

    const gridHeaderRender = () => {
        return (
            <GridHeaderButton addshow={true} addclick={doAdd} editshow={true} delshow={true} />
        )
    }

    return (
        <div>
            <HeaderPage frmname={formvar.formname} formstate={formvar.formstate} compname='Comp Test' branchname='Branch Test' />

            <SearchBox condlist={condlist} searchevent={doSearch} />

            <DataTable value={datalist} header={gridHeaderRender()} paginator={true} rows={apphelper.ConfigVar.GridRow} paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink" scrollable={true}>
                <Column field="BankID" style={{ width: 300 }} header="รหัส" />
                <Column field="BankName" header="ชื่อ" />
                <Column field="UseStatus" header="UseStatus" style={{ width: 300 }} />
            </DataTable>

            <br />

            {/* <Button block variant="primary" onClick={() => navigate('/home/expensemanage/' + apphelper.Encode(JSON.stringify(param)))}>Expense Add</Button>

            <Button block variant="primary" onClick={() => console.log(formvar.IsAddState())}>Test Form State</Button>

            <Button block variant="primary" onClick={() => showLoading()}>Redux Show Loading</Button> */}

        </div>
    )
}
