import React, { useState, useRef, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { Form, Row, Col, Button } from 'react-bootstrap';

import apphelper from '../../apphelper/apphelper'
import FrmExpenseSearch from '../search/frmexpensesearch'

export default function FrmExpenseManage() {
    let navigate = useNavigate();
    let { param } = useParams();
    const [dto, setdto] = useState({});
    const [count, setcount] = useState(0);

    const frmsearch = useRef()
    // let dto = {}

    useEffect(() => {

        let obj = apphelper.CheckParams(param, 'expense');
        if (obj != null)
            setdto(obj)

    }, []);

    function renderFooter() {
        return (
            <div>
                <Button variant="primary">close</Button>
            </div>
        );
    }

    function CloseChild(param) {
        setcount(param)
    }

    // const CloseChild = (param) => {
    //     console.log(param)
    // }

    return (
        <div style={{ backgroundColor: 'yellow' }}>
            Expense Manage id : {dto.id} name : {dto.name}

            <div style={{ backgroundColor: 'blue' }}>
                <Button block variant="primary" onClick={() => frmsearch.current.Open()} >test child component call parent function {count}</Button>
            </div>

            <FrmExpenseSearch
                closeclick={CloseChild}
                ref={frmsearch} />
        </div>
    )
}
