import React, { useState, useEffect, useContext, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux'
import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import Dialog from 'react-bootstrap-dialog'

import apphelper from '../../apphelper/apphelper'
import HeaderPage from '../headerpage'
import SearchBox from '../searchbox'
import GridHeaderButton from '../gridheaderbutton'
import gbstyle from '../../assets/gbstyle';
import ApiContext from '../../apphelper/apihelper';

export default function FrmBank() {
    const apicontext = useContext(ApiContext);
    const [formvar, setformvar] = useState(apphelper.InitFormVar());
    const [datalist, setdatalist] = useState([]);
    const [selection, setselection] = useState(null);
    const [searchparam, setsearchparam] = useState(null);
    let navigate = useNavigate();
    let confirmdialog = useRef()
    const systemenv = useSelector(state => state.systemenv)

    let condlist = [
        { value: 'BankID', label: 'รหัส' },
        { value: 'BankName', label: 'ชื่อ' }
    ]

    useEffect(() => {
        InitForm();
    }, []);

    const InitForm = () => {
        setformvar({
            ...formvar,
            formname: 'ข้อมูลธนาคาร',
            formstate: ''
        })

        apicontext.BindNextTab()

        doSearch(undefined)
    }

    const doSearch = async (params) => {
        let searchtext = '';
        setsearchparam(params);

        if (!apphelper.IsNull(params))
            searchtext += '?condition=' + params.condition + '&searchtxt=' + params.searchtext;

        let result = await apicontext.ApiGet(systemenv.apiurl + "bank/list" + searchtext)

        if (result.success)
            setdatalist(result.data);
    }

    const doAdd = () => {
        let param = { formstate: apphelper.FormState.ADD, data: {} }
        navigate(apphelper.NavigatPath.bankmanage + apphelper.Encode(JSON.stringify(param)))
    }

    const doEdit = () => {
        if (apphelper.IsNull(selection))
            return;

        let param = { formstate: apphelper.FormState.EDIT, data: selection }
        navigate(apphelper.NavigatPath.bankmanage + apphelper.Encode(JSON.stringify(param)))
    }

    const doDelete = () => {
        if (apphelper.IsNull(selection))
            return;

        confirmdialog.current.show({
            title: 'Confirm Delete',
            body: 'คุณต้องการลบข้อมูล ' + selection.BankID + ' ?',
            actions: [
                Dialog.OKAction(async () => {
                    let result = await apicontext.ApiPost(systemenv.apiurl + "bank/del", selection, apphelper.LoadingText.Delete)
                    if (result.success)
                        doSearch(searchparam)
                }),
                Dialog.CancelAction(),
            ],
            bsSize: 'small'
        })
    }

    const gridHeaderRender = () => {
        return (
            <GridHeaderButton addshow={formvar.canadd} addclick={doAdd} editshow={formvar.canedit} editclick={doEdit} delshow={formvar.candel} delclick={doDelete} />
        )
    }

    return (
        <div>
            <HeaderPage frmname={formvar.formname} formstate={formvar.formstate} />

            <SearchBox condlist={condlist} searchevent={doSearch} />

            <DataTable value={datalist} header={gridHeaderRender()} paginator={true} rows={apphelper.ConfigVar.GridRow} paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink" scrollable={true} selectionMode="single" selection={selection} onSelectionChange={e => setselection(e.value)}>
                <Column style={gbstyle.rowindexcol} header="" body={(row, col) => col.rowIndex + 1 }/>
                <Column field="BankID" style={{ width: 200 }} header="รหัส" />
                <Column field="BankName" header="ชื่อ" />
                <Column field="UseStatus" header="UseStatus" style={gbstyle.usestatuscol} />
            </DataTable>

            <br />

            <Dialog ref={confirmdialog} />

        </div>
    )
}
